import React from 'react';
import classes from './Search.module.css';

const Search = ({style, placeholder}) => {
    return <div style={{...style}} className={classes.searchWrapper}>
        <input className={classes.searchField} placeholder={placeholder || 'Search'}></input>
        <div className={classes.searchIconWrapper}><i className={classes.searchIcon + ' fa fa-search'} ></i></div>
        
    </div>
}

export default Search;