import React from 'react';
import classes from './HeroShortDescription.module.css';
import StarsRating from '../StarsRating/StarsRating';
import Rating from '../Rating/Rating';

const HeroShortDescription = ({movieName, movieGenres, movieTime, movieScore, style}) => {
    let hours = Math.floor(movieTime / 60);
    let minutes = movieTime % 60; 
    return <div style={{...style}} className={classes.wrapper}>
        <h1 className={classes.movieName}>{movieName}</h1>
        <div className={classes.infoWrapper}>
            {movieGenres.map((genre, id) => {
                return <p key={id} className={classes.genre}>{genre}</p>
            })}
            <span>|</span>
            <p className={classes.time}>{hours}h {minutes}m</p>
        </div>
        <div className={classes.rating}>
            <StarsRating score={movieScore} style={{marginRight: 10}}/>
            <Rating score={movieScore} style={{color: '#fff'}}/>
        </div>
    </div>
}

export default HeroShortDescription;