import React from 'react';
import classes from './StarsRating.module.css';

const StarsRating = ({score, style}) => {
    if (score > 5) score = score / 2;
    score = Math.ceil(score);

    let body = [];

    for (let i = 0; i < 5; i++) {
        if (i < score) {
            body.push(<svg key={i} className={classes.icon} xmlns="http://www.w3.org/2000/svg" width="30" height="30" viewBox="0 0 24 24"><path d="M12 .288l2.833 8.718h9.167l-7.417 5.389 2.833 8.718-7.416-5.388-7.417 5.388 2.833-8.718-7.416-5.389h9.167z"/></svg>)
        } else {
           body.push(<svg key={i} className={classes.icon} xmlns="http://www.w3.org/2000/svg" width="30" height="30" viewBox="0 0 24 24"><path d="M12 6.76l1.379 4.246h4.465l-3.612 2.625 1.379 4.246-3.611-2.625-3.612 2.625 1.379-4.246-3.612-2.625h4.465l1.38-4.246zm0-6.472l-2.833 8.718h-9.167l7.416 5.389-2.833 8.718 7.417-5.388 7.416 5.388-2.833-8.718 7.417-5.389h-9.167l-2.833-8.718z"/></svg>)
        }
    }
    return <div style={{...style}}>{body}</div>
}

export default StarsRating;