import React, {useState} from 'react';
import classes from './Hero.module.css';
import useWindowDimensions from '../../hooks/useWindowDimensions';
import HeroShortDescription from '../HeroShortDescription/HeroShortDescription';
import HeroWatch from '../HeroWatch/HeroWatch';
import HeroSignUp from '../HeroSignUp/HeroSignUp';
import Switcher from '../Switcher/Switcher';

const Hero = () => {
    const [current, setCurrent] = useState(0);
    const [movies] = useState([{name: 'The Jungle Book', genres: ['Adventure', 'Drama', 'Family', 'Fantasy'], time: 100, score: 7.2, background: 'https://image.tmdb.org/t/p/original/h87XbbFXIGE86osXjQ092UQyJkr.jpg'}, {name: 'Sherlock', genres: ['Crime', 'Drama', 'Mystery'], time: 90, score: 8.9, background: 'https://simkl.in/fanart/65/652470a02cfb2cc6_0.jpg'}, {name: 'The Gentelmen', genres: ['Action', 'Comedy', 'Crime'], time: 113, score: 8.5, background: 'https://kinofilmpro.ru/wp-content/uploads/2020/01/dzhent-fon.jpg'}, {name: 'Afro Samurai', genres: ['Animation', 'Action', 'Adventure'], time: 26, score: 7.3, background: 'http://content.foto.mail.ru/mail/husuk/Afro-samurai/s-12409.jpg'}, {name: '500 Days of Summer', genres: ['Comedy', 'Drama', 'Romance'], time: 95, score: 7.7, background: 'https://s4.afisha.ru/mediastorage/3c/fa/d0bb51456b6042c5b4b9e382fa3c.jpg'}]);
    
    const { height, width} = useWindowDimensions();

    return <div className={classes.hero} style={{height: height, background: `url('${movies[current].background}')`}}>

            <div className={classes.shadowTop} style={{width:width-100, height: height*0.30}}></div>
            <div className={classes.shadowBottom} style={{width: width-100, height: height*0.30}}></div>
            
            <Switcher onChange={(i) => setCurrent(i)} number={movies.length} current={current}/>
            <HeroShortDescription movieName={movies[current].name} movieGenres={movies[current].genres} movieTime={movies[current].time} movieScore={movies[current].score}/>
            <HeroWatch/>
            <HeroSignUp/>
            
        </div>
    
}

export default Hero;